	module.exports = {"pathContext":{"docHtml":"<h1>object.getownpropertydescriptors <sup><a href=\"https://npmjs.org/package/object.getownpropertydescriptors\"><img src=\"http://versionbadg.es/ljharb/object.getownpropertydescriptors.svg\" alt=\"Version Badge\"></a></sup></h1>\n<p><a href=\"https://travis-ci.org/ljharb/object.getownpropertydescriptors\"><img src=\"https://travis-ci.org/ljharb/object.getownpropertydescriptors.svg\" alt=\"Build Status\"></a>\n<a href=\"https://david-dm.org/ljharb/object.getownpropertydescriptors\"><img src=\"https://david-dm.org/ljharb/object.getownpropertydescriptors.svg\" alt=\"dependency status\"></a>\n<a href=\"https://david-dm.org/ljharb/object.getownpropertydescriptors#info=devDependencies\"><img src=\"https://david-dm.org/ljharb/object.getownpropertydescriptors/dev-status.svg\" alt=\"dev dependency status\"></a>\n<a href=\"LICENSE\"><img src=\"http://img.shields.io/npm/l/object.getownpropertydescriptors.svg\" alt=\"License\"></a>\n<a href=\"http://npm-stat.com/charts.html?package=object.getownpropertydescriptors\"><img src=\"http://img.shields.io/npm/dm/object.getownpropertydescriptors.svg\" alt=\"Downloads\"></a></p>\n<p><a href=\"https://npmjs.org/package/object.getownpropertydescriptors\"><img src=\"https://nodei.co/npm/object.getownpropertydescriptors.png?downloads=true&#x26;stars=true\" alt=\"npm badge\"></a></p>\n<p><a href=\"https://ci.testling.com/ljharb/object.getownpropertydescriptors\"><img src=\"https://ci.testling.com/ljharb/object.getownpropertydescriptors.png\" alt=\"browser support\"></a></p>\n<p>An ES2017 spec-compliant shim for <code>Object.getOwnPropertyDescriptors</code> that works in ES5.\nInvoke its \"shim\" method to shim <code>Object.getOwnPropertyDescriptors</code> if it is unavailable, and if <code>Object.getOwnPropertyDescriptor</code> is available.</p>\n<p>This package implements the <a href=\"https://github.com/es-shims/api\">es-shim API</a> interface. It works in an ES3-supported environment and complies with the <a href=\"https://github.com/tc39/ecma262/pull/582\">spec</a>.</p>\n<h2>Example</h2>\n<pre><code class=\"language-js\">var getDescriptors = require('object.getownpropertydescriptors');\nvar assert = require('assert');\nvar obj = { normal: Infinity };\nvar enumDescriptor = {\n    enumerable: false,\n    writable: false,\n    configurable: true,\n    value: true\n};\nvar writableDescriptor = {\n    enumerable: true,\n    writable: true,\n    configurable: true,\n    value: 42\n};\nvar symbol = Symbol();\nvar symDescriptor = {\n    enumerable: true,\n    writable: true,\n    configurable: false,\n    value: [symbol]\n};\n\nObject.defineProperty(obj, 'enumerable', enumDescriptor);\nObject.defineProperty(obj, 'writable', writableDescriptor);\nObject.defineProperty(obj, 'symbol', symDescriptor);\n\nvar descriptors = getDescriptors(obj);\n\nassert.deepEqual(descriptors, {\n    normal: {\n        enumerable: true,\n        writable: true,\n        configurable: true,\n        value: Infinity\n    },\n    enumerable: enumDescriptor,\n    writable: writableDescriptor,\n    symbol: symDescriptor\n});\n</code></pre>\n<pre><code class=\"language-js\">var getDescriptors = require('object.getownpropertydescriptors');\nvar assert = require('assert');\n/* when Object.getOwnPropertyDescriptors is not present */\ndelete Object.getOwnPropertyDescriptors;\nvar shimmedDescriptors = getDescriptors.shim();\nassert.equal(shimmedDescriptors, getDescriptors);\nassert.deepEqual(shimmedDescriptors(obj), getDescriptors(obj));\n</code></pre>\n<pre><code class=\"language-js\">var getDescriptors = require('object.getownpropertydescriptors');\nvar assert = require('assert');\n/* when Object.getOwnPropertyDescriptors is present */\nvar shimmedDescriptors = getDescriptors.shim();\nassert.notEqual(shimmedDescriptors, getDescriptors);\nassert.deepEqual(shimmedDescriptors(obj), getDescriptors(obj));\n</code></pre>\n<h2>Tests</h2>\n<p>Simply clone the repo, <code>npm install</code>, and run <code>npm test</code></p>","docPath":"argo-ci/node_modules/object.getownpropertydescriptors/readme","proj":"argo-ci"}}

